const CatalogInput = ({ setData, setLengthDepthFlag }) => {
  const [xrfData, setXrfData] = useState("");
  const [combineBaseTwoDigits, setCombineBaseTwoDigits] = useState(false);

  // TODO: remove this. testing purposes only
  useEffect(() => {
    handleXrfChange(INITIAL_DATA);
  }, []);

  function handleRadio(isChecked) {
    setLengthDepthFlag(isChecked);
    setCombineBaseTwoDigits(isChecked);
  }

  function handleXrfChange(data) {
    setXrfData(data);
    let rows = data.split("\n");
    rows.shift();
    rows = rows.filter((item) => item);
    let values = rows.map((row) => row.split("\t"));
    setData(values);
  }

  return (
    <Card className={styles["card"]}>
      <Card.Title>Input Catalog Numbers</Card.Title>
      <Card.Body>
        <Form>
          <Form.Group>
            <Form.Label className={styles["label"]}>XRF Data</Form.Label>
            <Form.Control
              as="textarea"
              placeholder="data..."
              value={xrfData || ""}
              onChange={(e) => handleXrfChange(e.target.value)}
            />
          </Form.Group>
        </Form>

        <Form.Group id="units">
          <Form.Label className={styles["label"]}>
            Does the base2 of the catalog number take the form "####-2460-####"?
          </Form.Label>
          <div key={`inline-radio`}>
            <Form.Check
              inline
              checked={combineBaseTwoDigits}
              label="Yes"
              name="units-of-measure"
              type="radio"
              id={`inline-radio-1`}
              onChange={(e) => handleRadio(true)}
            />
            <Form.Check
              inline
              checked={!combineBaseTwoDigits}
              label="No"
              name="units-of-measure"
              type="radio"
              id={`inline-radio-2`}
              onChange={(e) => handleRadio(false)}
            />
          </div>
        </Form.Group>
      </Card.Body>
    </Card>
  );
};
export default CatalogInput;
